import { FC, useEffect } from "react";
import { useAppSelector } from "@/app/lib/hooks/redux";
import { RouterState } from "../model/RouterSlice";
import CurrentRoute from "./CurrentRoute/CurrentRoute";
import { Preloader } from "@/shared/Preloader";







interface RouterProps {
    // router: RouterState
    preloader: boolean
    // initialized 
}


const Router: FC<RouterProps> = ({
    preloader,
}) => {
    
    const router = useAppSelector(state => state.router) as RouterState
    const currentProcess = useAppSelector(state => state.app.app)
    // const currentRoute = router.current.route
    let component = <Preloader />


    useEffect(() => {
        if (router.current && router.current.route &&
            !preloader) {

            component = <CurrentRoute currentProcess={currentProcess} />
        }
    }, [router, preloader])


    if (!preloader) {

        if (router.current && router.current.route) {

            component = <CurrentRoute currentProcess={currentProcess} />


        }
    }

    return component
}

export default Router